import React, { FC } from 'react'
import * as S from 'components/Dialog/styles'
import Button from 'components/Button'
import ButtonGroup from 'components/ButtonGroup'

interface Props{
  /**   */
  isVisible: boolean;
  /**  */
  title?: string;
  /**  */
  description?: string;
  /**  */
  children?: React.ReactNode;
  /**     */
  isHideButton: boolean;
  /**    */
  canCancel: boolean;
  /**     */
  cancelText: string;
  /**     */
  confirmText: string;
  /**    */
  onCancel?: () => void;
  /**     */
  onConfirm?: () => void;
  /** Dialog Ref */
  dialogRef: React.MutableRefObject<null>
}

/**
 *  .
 *    
 *        .
 */
const Dialog: FC<Props> = ({
  isVisible,
  title,
  description,
  children,
  isHideButton,
  canCancel,
  cancelText,
  confirmText,
  onCancel,
  onConfirm,
  dialogRef,
}) => {
  if (!isVisible) {
    return null
  }
  return (
  <S.Container>
    <S.ContentWrapper>
      <S.Content ref={dialogRef}>
        {title && <S.Title>{title}</S.Title>}
        {description && <S.Description>{description}</S.Description>}
        {children}
        {!isHideButton && (
          <ButtonGroup right>
            {canCancel && (
              <Button
                text={confirmText}
                theme='primary'
                onClick={onConfirm}
              />
            )}
            <Button
              text={cancelText}
              theme='tertiary'
              onClick={onCancel}
            />
          </ButtonGroup>
        )}

      </S.Content>

    </S.ContentWrapper>

  </S.Container>
  )
}

export default Dialog
