import React, { FC } from 'react'
import * as S from 'components/ButtonGroup/styles'

interface Props{
  /**   */
  direction?: 'row' | 'col';
  /**      */
  right?: boolean;
  /**   */
  children: React.ReactNode;
}

/**
 *    .
 * children    .
 */
const ButtonGroup: FC<Props> = ({
  direction = 'row',
  right = false,
  children,
}) => (
    <S.default
      direction={direction}
      right={right}
    >
      {children}
    </S.default>
)

export default ButtonGroup
